.day-item{flex:1 0 100px;text-align:center;border-top:2px solid #E3E1E1}.day-item .description{padding-top:14px;padding-bottom:13px;font-size:14px;text-transform:uppercase;color:#FD7013}.day-item .icon{height:48px}.day-item .temperature{padding-top:12px;padding-bottom:12px;font-size:14px;color:#393E46}.day-item .date{font-size:14px;text-transform:uppercase;color:#ffffff;padding:10px 32px;background-color:#393E46}.day-item .active{background-color:#676D77}.day-item:hover{cursor:pointer;border-top:2px solid #FD7013}.active{background-color:#eeeeee}
